@font-face {
  font-family: 'New York';
  src: url(./NewYork.otf);
}
@import url('https://fonts.googleapis.com/css2?family=Erica+One&display=swap');
body
{
  background-image: url(./components/images/ffflux.svg);
  background-position: center;
  background-size: cover;
}.App {
  padding: 1em;
  text-align: center;
  width: 90%;
  margin: 0 auto;
}

h1,h2,h3,h4,h5,a
{
  font-family: 'Erica One', cursive;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
nav
{
  background-color: rgb(255, 201, 201);
  height: 200px;
  display: grid;
  place-items: center;
  font-size: 2em;
  border-radius: 1em;
  box-shadow:
  0px 0px 3.2px -25px rgba(0, 0, 0, 0.046),
  0px 0px 7.1px -25px rgba(0, 0, 0, 0.066),
  0px 0px 11.9px -25px rgba(0, 0, 0, 0.078),
  0px 0px 18px -25px rgba(0, 0, 0, 0.085),
  0px 0px 25.9px -25px rgba(0, 0, 0, 0.09),
  0px 0px 36.7px -25px rgba(0, 0, 0, 0.093),
  0px 0px 52px -25px rgba(0, 0, 0, 0.096),
  0px 0px 75.6px -25px rgba(0, 0, 0, 0.1),
  0px 0px 116.4px -25px rgba(0, 0, 0, 0.107),
  0px 0px 207px -25px rgba(0, 0, 0, 0.13)
;

}
a:hover
{
  color: #ffffff;
  text-decoration: none;
}
a,table
{
  color: rgb(255, 129, 129) !important;
}
.statistics
{
  margin-top: 3em;
  border-radius: 2em;align-items: center !important;
  box-shadow:
  
  0px 0px 3.2px -25px rgba(0, 0, 0, 0.046),
  0px 0px 7.1px -25px rgba(0, 0, 0, 0.066),
  0px 0px 11.9px -25px rgba(0, 0, 0, 0.078),
  0px 0px 18px -25px rgba(0, 0, 0, 0.085),
  0px 0px 25.9px -25px rgba(0, 0, 0, 0.09),
  0px 0px 36.7px -25px rgba(0, 0, 0, 0.093),
  0px 0px 52px -25px rgba(0, 0, 0, 0.096),
  0px 0px 75.6px -25px rgba(0, 0, 0, 0.1),
  0px 0px 116.4px -25px rgba(0, 0, 0, 0.107),
  0px 0px 207px -25px rgba(0, 0, 0, 0.13)
;

}
.statistics-text
{
  background-color: rgb(255, 201, 201);
  display: grid;border-radius: 2em !important;
  grid-template-columns: 1fr 1fr;
  place-items: center;
  text-align: left;
}
h1,h2,h3,h4,h4,h6
{
  font-size: 4em !important;
  color: rgb(255, 129, 129);
}
p
{
  font-weight: 600 !important;
  color: #4a4a4a !important;
}
th,td
{
  border: none !important ;
}
table
{
  text-align: center;
  letter-spacing: 2px;
  font-family: 'Erica One', cursive;
}

.stat-table
{
  display: flex;
  padding: 2em !important;
  flex-direction: column;
  align-items: center !important;
  justify-content: center !important;
}
.btn 
{
  font-family: 'Erica One', cursive !important;
  padding: 1em 2em !important;
  font-size: 1em !important;
  color: #ffffff !important;
  background-color: rgb(255, 129, 129) !important;
  border-radius: 1em;
}
.team-pfp
{
  height: 256px !important;
  border-radius: 4em;
  padding: 2em;
}
a .ref-btn 
{
  
}
h1
{
  margin: 1em auto;
  font-size: 5em !important;
}